.products-categories {
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.products-categories__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $tablet-width) {
    display: flex;
    margin: 0 auto;
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1050px;
    padding: 0;
  }
}

.products-categories__item {
  @media (min-width: $tablet-width) {
    position: relative;
    width: 50%;

    &:hover {
      transform: translateY(-5px);

      & .products-categories__link::after {
        opacity: 0.3;
      }
    }
  }

  &--interior {
    background-color: $color-green-200;
  }

  &--toys {
    background-color: $color-green-300;
  }
}

.products-categories__link {
  position: relative;
  display: block;
  padding-top: 24px;
  padding-bottom: 31px;
  padding-left: 30px;
  color: $color-white;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-top: 25px;
    padding-bottom: 37px;
    padding-left: 36px;
  }

  &::after {
    position: absolute;
    content: "";
  }

  &--interior::after {
    top: 23px;
    right: 36px;
    width: 40px;
    height: 62px;
    background-image: url("../img/interior.svg");

    @media (min-width: $tablet-width) {
      top: 30px;
    }
  }

  &--toys::after {
    top: 21px;
    right: 27px;
    width: 60px;
    height: 60px;
    background-image: url("../img/toy.svg");

    @media (min-width: $tablet-width) {
      top: 32px;
      right: 33px;
    }
  }
}
